
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'

import XSwitch from 'c%/switch.vue'

export default{
mixins: [ common ],
components: {
Card,
XSwitch
},
data () {
return {
data: []
}
},
computed: {
data_uri() {
return 'customer_follow_up?cu_id='+this.$route.params.id
}
}
}



